<template>
  <div class="game-box">
    <van-field
      v-model="question"
      rows="2"
      autosize
      label="问题"
      type="textarea"
      placeholder="请输入问题"
      @blur="getAnswer"
    />

    <div class="answer-box">
      <van-image v-if="imgUrl" @click="previewImg(imgUrl)" fit="cover" :src="imgUrl">
        <template v-slot:loading>
          <van-loading type="spinner" size="20" />
        </template>
      </van-image>
    </div>

    <van-image-preview v-model="imgShow" :images="images" />

  </div>
</template>

<script>
import { Field, Toast, Loading, Image as VanImage, ImagePreview } from "vant";

export default {
  name: "Game",
  props: {},
  components: {
    [Field.name]: Field,
    [Loading.name]: Loading,
    [VanImage.name]: VanImage,
    [ImagePreview.Component.name]: ImagePreview.Component,
  },
  data() {
    return {
      imgShow: false,
      images: [],
      question: "",
      imgUrl: "",
    };
  },
  methods: {
    previewImg(img){
      this.images.push(img)
      this.imgShow = true
    },
    async getAnswer() {
      if (
        this.question.indexOf("?") === -1 &&
        this.question.indexOf("？") === -1
      ) {
        Toast({
          message: "问题通常都有一个标点符号 :-)",
          position: "top",
        });
        return;
      }
      this.imgUrl = '';
      let res = await this.$Http.wtfApi();
      this.imgUrl = res.image;
    },
  },
  created() {},
  watch: {},
};
</script>

<style scoped lang='less'>
.game-box {
  margin-top: 60px;
  .answer-box{
    padding: 20px;
    text-align: center;

    img{
      width: 100%;
    }
  }
}
</style>